import React from "react";
import "../app.css";
import NavBar from "../components/NavBar";
import Particles from "react-particles-js";
import Typed from "react-typed";
import About from "../components/About";
import Service from "../components/Service";
import Contact from "../components/Contact";

const Home = () => {
  return (
    <>
      <NavBar />
      <Particles
        params={{
          particles: {
            number: {
              value: 30,
              density: {
                enable: true,
                value_area: 900,
              },
            },
            shape: {
              type: "square",
              stroke: {
                width: 6,
                color: "#f9ab00",
              },
            },
          },
        }}
      />

      <div
        style={{
          display: "flex",
          justifyContent: "space-around",
          alignItems: "center",
          width: "100%",
          height: "400px",
          backgroundColor: "#1f2235",
        }}
      >
        <div>
          <Typed
            className="typed-text"
            strings={[
              "Full Stack Developer",
              "Python Developer",
              "App Developer",
              "Hi I am Indal Kumar",
            ]}
            typeSpeed={40}
            backSpeed={50}
            loop
          />
        </div>
      </div>

      <Service />
      <Contact />
    </>
  );
};

export default Home;
