import React from "react";
import {
  Container,
  Grid,
  Button,
  Typography,
  Paper,
  Card,
  makeStyles,
} from "@material-ui/core";
import GitHubIcon from "@material-ui/icons/GitHub";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardActions from "@material-ui/core/CardActions";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import {
  FaGithub,
  FaCamera,
  FaCircleNotch,
  FaApple,
  FaFileVideo,
  FaSearchDollar,
  FaDatabase,
  FaPython,
} from "react-icons/fa";
import AndroidIcon from "@material-ui/icons/Android";
import "./service.css";

const useStyles = makeStyles({
  card: {
    backgroundColor: "red",
    color: "white",
    padding: 20,
  },
});

const Service = () => {
  const classes = useStyles();
  const [header] = React.useState({
    mainHeader: "SERVICES",
    subHeading: "My Services",
    text: "",
  });
  const [state] = React.useState([
    {
      id: 1,
      icon: <FaGithub className="commonIcons" />,
      heading: "Web Development",
      text: "I can build a beautiful Single Page Website and React.js",
    },
    {
      id: 2,
      icon: <FaDatabase className="commonIcons" />,
      heading: "Backend Development",
      text: "Backend Development BY django and SQLlite or PostgreSQL",
    },
    {
      id: 3,
      icon: <FaCircleNotch className="commonIcons" />,
      heading: "Web Desing",
      text: "I can build a beautiful web Page using HTML, CSS and Javascript",
    },
    {
      id: 4,
      icon: <AndroidIcon style={{ fontSize: 50 }} className="commonIcons" />,
      heading: "App Devlopment",
      text: "Android app devalopment By Django Rest API and Flutter",
    },
    {
      id: 5,
      icon: <FaPython className="commonIcons" />,
      heading: "API Development",
      text: "API Development REST API using django-rest-api",
    },
  ]);
  return (
    <div className="services">
      <div className="container">
        <div className="services__header">
          <div className="common">
            <h1 className="mainHeader">{header.subHeading}</h1>
            <p className="mainContent">{header.text}</p>
            <div className="commonBorder"></div>
          </div>

          <div className="row bgMain">
            {state.map((info) => (
              <div className="col-4 bgMain">
                <div className="services__box">
                  {info.icon}
                  <div className="services__box-header">{info.heading}</div>
                  <div className="services__box-p">{info.text}</div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default Service;
